$lg-path-fonts: "~lightgallery/fonts";
$lg-path-images: "~lightgallery/images";

@import "lightgallery/scss/lightgallery.scss";
@import "lightgallery/scss/lg-transitions.scss";

:root {
	--global--admin-bar--height: 0px;
}

.admin-bar {
	--global--admin-bar--height: 32px;
}

@media only screen and ( max-width: 782px ) {
	.admin-bar {
			--global--admin-bar--height: 46px;
	}
}

.gs-lightgallery {
	a.lg-icon,
	button.lg-icon:not(:active):not(:hover) {
		background-color: transparent;
		color: var(--gutenslider-lightgallery-font);
	}

	a.lg-icon:hover,
	button.lg-icon:hover {
		opacity: 0.7;
		color: var(--gutenslider-lightgallery-font);
	}

	.lg-outer .lg-thumb-outer {
		background-color: transparent;
	}

	.lg-sub-html,
	.lg-counter {
		color: var(--gutenslider-lightgallery-font);
	}

	.lg-outer {
		top: var(--global--admin-bar--height);
		height: calc(100% - var(--global--admin-bar--height));
	}

	.lg-outer .lg-thumb-item {
		border-color: transparent;
		border-radius: 0;
		transition: transform 0.2s;
	}

	.lg-outer .lg-thumb-item.active,
	.lg-outer .lg-thumb-item:hover {
		border-color: var(--gutenslider-lightgallery-font);
		transform: scale(1.02);
	}
}

.wp-block-eedee-block-gutenslider.lg-zoom-icon {
	.swiper-slide {
		cursor: zoom-in;
	}
}
